import React, { useState, useRef } from 'react';
import styled from 'styled-components';

import { timeFormatter, videoTimepointFormatter } from '../shared/util';

import AnnotationForm, { Annotation } from './AnnotationForm';
import Map, { LatLng } from './Map';
import VideoPlayer, { VideoState, VideoPlayerRef } from './VideoPlayer';

const Layout = styled.div`
  display: grid;
  grid: repeat(2, minmax(0, 1fr)) / repeat(2, minmax(0, 1fr));
  width: 100vw;
  height: 100vh;

  > .video {
    grid-row: 1 / span 1;
    grid-column: 1 / span 1;
  }

  > .map {
    grid-row: 2 / span 1;
    grid-column: 1 / span 1;
  }

  > .timeline {
    grid-row: 1 / span 2;
    grid-column: 2 / span 1;
  }
`;

const VideoAnnotatorPage = () => {
  const [url, setUrl] = useState('https://youtu.be/8uRjumEQw70');
  const [markerLatLng, setMarkerLatLng] = useState<LatLng>({
    latitude: 38.8899389,
    longitude: -77.0090505,
  });

  const [videoState, setVideoState] = useState<VideoState | null>(null);
  const [annotations, setAnnotations] = useState<Annotation[]>([]);
  const videoPlayerRef = useRef<VideoPlayerRef>(null);

  return (
    <Layout>
      <div className="video">
        <VideoPlayer
          ref={videoPlayerRef}
          url={url}
          onUpdate={(newState) => setVideoState(newState)}
        />
      </div>
      <div className="map">
        <Map
          markerLatLng={markerLatLng}
          onMarkerDrop={(newLatLng) => {
            setMarkerLatLng(newLatLng);
          }}
        />
      </div>
      <div className="timeline">
        <AnnotationForm
          url={url}
          onUrlChange={(newUrl) => {
            setUrl(newUrl);
          }}
          videoState={videoState}
          latLng={markerLatLng}
          onSubmit={(annotation) => {
            setAnnotations((prevAnnotations) => [...prevAnnotations, annotation]);
          }}
        />
        <div className="p-3">
          <div className="table-responsive">
            <table className="table table-sm">
              <thead className="text-nowrap">
                <tr>
                  <th>Video URL</th>
                  <th>Video time</th>
                  <th>Local time (EST)</th>
                  <th>Location</th>
                  <th>Notes</th>
                  <th>Hashtags</th>
                </tr>
              </thead>
              <tbody>
                {annotations.map((annotation, i) => (
                  // eslint-disable-next-line react/no-array-index-key
                  <tr key={i}>
                    <td>
                      <a
                        href={annotation.videoUrl}
                        onClick={(event) => {
                          event.preventDefault();
                          setUrl(annotation.videoUrl);
                        }}
                      >
                        {annotation.videoUrl}
                      </a>
                    </td>
                    <td>
                      <a
                        // TODO: Link somewhere real, maybe the video URL + timestamp so that folks can copy/paste the link and have it accurately jump to the right spot?
                        href={`#${videoTimepointFormatter(annotation.videoTime)}`}
                        onClick={(event) => {
                          if (videoPlayerRef.current) {
                            event.preventDefault();
                            videoPlayerRef.current.seekTo(annotation.videoTime, 'seconds');
                          }
                        }}
                      >
                        {videoTimepointFormatter(annotation.videoTime)}
                      </a>
                    </td>
                    <td>{timeFormatter(annotation.localTime || undefined)}</td>
                    <td>
                      {annotation.latLng ? (
                        <a
                          // TODO: Link somewhere real, maybe to Google Maps with this lat/lng?
                          href="#🌎"
                          onClick={(event) => {
                            event.preventDefault();
                            setMarkerLatLng(annotation.latLng);
                          }}
                        >
                          🌎
                        </a>
                      ) : null}
                    </td>
                    <td>{annotation.notes}</td>
                    <td>{annotation.hashtags.join(', ')}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default VideoAnnotatorPage;
